$bg: #333;
$push-color: hsla(10, 90%, 40%, 1);

$push-size: 120px;

.blue {
  filter: hue-rotate(215deg);
  &.glow:active,
  &.glow.isPushed {
    filter: hue-rotate(215deg) brightness(1.5) !important;
  }
}

.gray {
  filter: grayscale(1);
  &.glow:active,
  &.glow.isPushed {
    filter: grayscale(1) brightness(1.5) !important;
  }
}

.green {
  filter: hue-rotate(115deg);
  &.glow:active,
  &.glow.isPushed {
    filter: hue-rotate(115deg) brightness(1.5) !important;
  }
}

.purple {
  filter: hue-rotate(250deg);
  &.glow:active,
  &.glow.isPushed {
    filter: hue-rotate(250deg) brightness(1.5) !important;
  }
}

.pink {
  filter: hue-rotate(300deg);
  &.glow:active,
  &.glow.isPushed {
    filter: hue-rotate(300deg) brightness(1.5) !important;
  }
}

.yellow {
  filter: hue-rotate(50deg) brightness(2) !important;
  &.glow:active,
  &.glow.isPushed {
    filter: hue-rotate(50deg) brightness(2.5) !important;
  }
}

.cyan {
  filter: hue-rotate(168deg) brightness(2);
  &.glow:active,
  &.glow.isPushed {
    filter: hue-rotate(168deg) brightness(2.5) !important;
  }
}

.pushFlat,
.pushSkeuo {
  user-select: none;
  position: relative;
  display: inline-block;
  width: $push-size;
  height: $push-size;
  border: 0;
  margin: 1em;
  outline: none;
  background-color: $push-color;
  border-radius: 50%;
  cursor: pointer;
  transition: all 200ms;
}
.pushFlat {
  box-shadow: inset 0 $push-size/32 0 lighten($push-color, 5%), inset 0 (-$push-size/32) 0 darken($push-color, 5%),
    inset 0 0 0 $push-size/32 darken($push-color, 3%), inset 0 0 0 $push-size/12 $push-color,
    inset 0 0 0 $push-size/10 darken($push-color, 20%), inset 0 0 0 $push-size/9.2 darken($push-color, 50%),
    inset 0 0 0 $push-size/7.5 transparentize(lighten($push-color, 30%), 0.3), inset 0 0 0 $push-size/5.5 $push-color,
    inset 0 $push-size/2.5 $push-size/7.5 darken($push-color, 5%), inset 0 0 $push-size/10 $push-size/6 darken($push-color, 10%),
    0 $push-size/20 0 rgba(0, 0, 0, 0.3);

  &:after {
    content: "";
    position: absolute;
    bottom: $push-size/20;
    left: $push-size/10;
    display: block;
    width: $push-size/1.25;
    height: $push-size/1.25;
    border: $push-size/15 solid rgba(0, 0, 0, 0.3);
    border-width: 0 0 $push-size/15;
    border-radius: 50%;
    transition-duration: 200ms;
  }

  &:active,
  // &:hover,
  &.isPushed {
    box-shadow: inset 0 $push-size/32 0 lighten($push-color, 5%), inset 0 (-$push-size/32) 0 darken($push-color, 5%),
      inset 0 0 0 $push-size/32 darken($push-color, 3%), inset 0 0 0 $push-size/12 $push-color,
      inset 0 0 0 $push-size/10 darken($push-color, 20%), inset 0 0 0 $push-size/8.5 darken($push-color, 40%),
      inset 0 0 0 $push-size/7.5 transparentize(lighten($push-color, 30%), 0.8), inset 0 0 0 $push-size/5.5 darken($push-color, 3%),
      inset 0 $push-size/2.5 $push-size/7.5 darken($push-color, 8%), inset 0 0 $push-size/10 $push-size/6 darken($push-color, 15%),
      0 $push-size/20 0 rgba(0, 0, 0, 0.3);
    background-color: darken($push-color, 2%);

    &:after {
      bottom: $push-size/20 + $push-size/15;
      border-width: 0;
    }
  }

  &.glow:active,
  &.glow.isPushed {
    filter: brightness(1.5);
  }
}

.pushSkeuo {
  box-shadow: inset 0 $push-size/24 0 lighten($push-color, 5%), inset 0 (-$push-size/24) $push-size/60 darken($push-color, 5%),
    0 0 $push-size/60 $push-color, inset 0 0 $push-size/30 darken($push-color, 15%), inset 0 0 $push-size/30 transparentize($bg, 0.5),
    inset 0 0 $push-size/60 $push-size/12 $push-color, inset 0 (-$push-size/30) $push-size/40 $push-size/10 transparentize($bg, 0.3),
    inset 0 0 $push-size/60 $push-size/10 darken($push-color, 20%), inset 0 0 $push-size/60 $push-size/10 transparentize($bg, 0.3),
    inset 0 0 $push-size/120 $push-size/9.2 darken($push-color, 50%),
    inset 0 0 $push-size/120 $push-size/8 transparentize(lighten($push-color, 30%), 0.3),
    inset 0 $push-size/20 0 $push-size/8.5 transparentize(lighten($push-color, 15%), 0.3),
    inset 0 (-$push-size/20) $push-size/60 $push-size/8.5 transparentize(darken($push-color, 10%), 0.8),
    inset 0 0 0 $push-size/5.5 $push-color, inset 0 $push-size/2.5 $push-size/7.5 darken($push-color, 5%),
    inset 0 0 $push-size/10 $push-size/6 darken($push-color, 10%), 0 $push-size/40 $push-size/20 rgba(0, 0, 0, 0.5);

  &:active,
  // &:hover,
  &.isPushed {
    box-shadow: inset 0 $push-size/24 0 lighten($push-color, 5%), inset 0 (-$push-size/24) $push-size/60 darken($push-color, 5%),
      0 0 $push-size/60 $push-color, inset 0 0 $push-size/30 darken($push-color, 15%), inset 0 0 $push-size/30 transparentize($bg, 0.5),
      inset 0 0 $push-size/60 $push-size/12 $push-color, inset 0 (-$push-size/30) $push-size/40 $push-size/10 transparentize($bg, 0.3),
      inset 0 0 $push-size/60 $push-size/10 darken($push-color, 20%), inset 0 0 $push-size/60 $push-size/10 transparentize($bg, 0.3),
      inset 0 0 $push-size/40 $push-size/8.5 darken($push-color, 40%),
      inset 0 0 $push-size/120 $push-size/7.5 transparentize(lighten($push-color, 30%), 0.8),
      inset 0 $push-size/20 0 28px transparentize(lighten($push-color, 15%), 0.5),
      inset 0 (-$push-size/20) $push-size/60 28px transparentize(darken($push-color, 20%), 0.8),
      inset 0 0 0 $push-size/5.5 darken($push-color, 3%), inset 0 $push-size/2.5 $push-size/7.5 darken($push-color, 8%),
      inset 0 0 $push-size/10 $push-size/6 darken($push-color, 15%), 0 $push-size/40 $push-size/20 rgba(0, 0, 0, 0.5);
    background-color: darken($push-color, 2%);

    &:before {
      opacity: 0.5;
    }
  }

  &:before {
    content: "";
    position: absolute;
    bottom: $push-size/4.5;
    left: $push-size/3.6;
    display: block;
    width: $push-size/2.2;
    height: $push-size/3.3;
    background: transparentize(lighten($push-color, 30%), 0.8);
    background: linear-gradient(to top, transparentize(lighten($push-color, 40%), 0.7) 0%, transparentize($push-color, 0.9) 100%);
    border-radius: 40% 40% 60% 60%;
    transition: opacity 200ms;
  }
}
